

import { Overlay } from '@angular/cdk/overlay';
import { Injectable, Injector, TemplateRef } from '@angular/core';
import { TDSSingletonService } from 'tds-ui/core/services';

import { TDSMNService } from './base';
import { TDSMessageContainerComponent } from './message-container.component';
import { TDSMessageServiceModule } from './message.service.module';
import { TDSMessageData, TDSMessageDataOptions, TDSMessageRef } from './typings';

@Injectable({
  providedIn: TDSMessageServiceModule
})
export class TDSMessageService extends TDSMNService {
  protected container?: TDSMessageContainerComponent;
  protected componentPrefix = 'message-';

  constructor(tdsSingletonService: TDSSingletonService, overlay: Overlay, injector: Injector) {
    super(tdsSingletonService, overlay, injector);
  }

  success(content: string | TemplateRef<void>, options?: TDSMessageDataOptions): TDSMessageRef {
    return this.createInstance({ type: 'success', content }, options);
  }

  error(content: string | TemplateRef<void>, options?: TDSMessageDataOptions): TDSMessageRef {
    return this.createInstance({ type: 'error', content }, options);
  }

  info(content: string | TemplateRef<void>, options?: TDSMessageDataOptions): TDSMessageRef {
    return this.createInstance({ type: 'info', content }, options);
  }

  warning(content: string | TemplateRef<void>, options?: TDSMessageDataOptions): TDSMessageRef {
    return this.createInstance({ type: 'warning', content }, options);
  }

  loading(content: string | TemplateRef<void>, options?: TDSMessageDataOptions): TDSMessageRef {
    return this.createInstance({ type: 'loading', content }, options);
  }

  create(
    type: 'success' | 'info' | 'warning' | 'error' | 'loading' | string,
    content: string | TemplateRef<void>,
    options?: TDSMessageDataOptions
  ): TDSMessageRef {
    return this.createInstance({ type, content }, options);
  }

  private createInstance(message: TDSMessageData, options?: TDSMessageDataOptions): TDSMessageRef {
    this.container = this.withContainer(TDSMessageContainerComponent);

    return this.container.create({
      ...message,
      ...{
        createdAt: new Date(),
        messageId: this.getInstanceId(),
        options
      }
    });
  }
}
